.app-ductwork-container {
  width: auto;
  height: auto;
  display: flex;
  position: relative;
  box-shadow: 1px 1px 1px 0px #d4d4d4,-1px -1px 1px 0px #d4d4d4;
  align-items: flex-start;
  border-radius: var(--dl-layout-radius-radius8);
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.8);
}
 
.app-ductwork-heading {
  flex: 0 0 auto;
  width: 300px;
  height: auto;
  display: flex;
  box-shadow: 0px 1px 2px 0px #d4d4d4;
  align-items: center;
  padding-top: var(--dl-layout-space-halfunit);
  padding-left: var(--dl-layout-space-unit);
  border-radius: var(--dl-layout-radius-radius8);
  padding-right: var(--dl-layout-space-halfunit);
  flex-direction: row;
  padding-bottom: var(--dl-layout-space-halfunit);
  justify-content: space-between;
}
 
.app-ductwork-icon1 {
  width: 16px;
  height: 16px;
  transition: 0.3s;
  border-radius: var(--dl-layout-radius-radius4);
}
 
.app-ductwork-icon1:hover {
  color: white;
  background: gray;
}
 
.app-ductwork-icon1:active {
  color: white;
  background: black;
}
 
.app-ductwork-ductwork {
  display: block;
}
 
.app-ductwork-inputs {
  flex: 0 0 auto;
  width: 300px;
  height: auto;
  display: flex;
  box-shadow: 0px 1px 0px 0px #ccc3c3;
  align-items: flex-start;
  flex-direction: column;
}
 
.app-ductwork-duct-name {
  gap: var(--dl-layout-space-halfunit);
  flex: 0 0 auto;
  width: 300px;
  height: auto;
  display: flex;
  box-shadow: 0px 1px 0px 0px #ccc3c3;
  align-items: flex-start;
  padding-top: var(--dl-layout-space-halfunit);
  padding-left: var(--dl-layout-space-unit);
  padding-right: var(--dl-layout-space-unit);
  flex-direction: row;
  padding-bottom: var(--dl-layout-space-halfunit);
}
 
.app-ductwork-duct-width {
  gap: var(--dl-layout-space-halfunit);
  flex: 0 0 auto;
  width: 300px;
  height: auto;
  display: flex;
  box-shadow: 0px 1px 0px 0px #ccc3c3;
  align-items: flex-start;
  padding-top: var(--dl-layout-space-halfunit);
  padding-left: var(--dl-layout-space-unit);
  padding-right: var(--dl-layout-space-unit);
  flex-direction: row;
  padding-bottom: var(--dl-layout-space-halfunit);
}
 
.app-ductwork-unit1 {
  font-size: 12px;
  font-style: normal;
  font-family: "Bricolage Grotesque";
  font-weight: 400;
}
 
.app-ductwork-duct-height {
  flex: 0 0 auto;
  width: 300px;
  height: auto;
  display: flex;
  box-shadow: 0px 1px 0px 0px #ccc3c3;
  align-items: flex-start;
  padding-top: var(--dl-layout-space-halfunit);
  padding-left: var(--dl-layout-space-unit);
  padding-right: var(--dl-layout-space-unit);
  flex-direction: row;
  padding-bottom: var(--dl-layout-space-halfunit);
}
 
.app-ductwork-unit2 {
  font-size: 12px;
  font-style: normal;
  font-family: "Bricolage Grotesque";
  font-weight: 400;
}
 
.app-ductwork-duct-insulation {
  flex: 0 0 auto;
  width: 300px;
  height: auto;
  display: flex;
  box-shadow: 0px 1px 0px 0px #ccc3c3;
  align-items: flex-start;
  padding-top: var(--dl-layout-space-halfunit);
  padding-left: var(--dl-layout-space-unit);
  padding-right: var(--dl-layout-space-unit);
  flex-direction: row;
  padding-bottom: var(--dl-layout-space-halfunit);
}
 
.app-ductwork-unit3 {
  font-size: 12px;
  font-style: normal;
  font-family: "Bricolage Grotesque";
  font-weight: 400;
}
 
.app-ductwork-save {
  gap: var(--dl-layout-space-halfunit);
  flex: 0 0 auto;
  width: 300px;
  height: auto;
  display: flex;
  box-shadow: 0px 1px 0px 0px #ccc3c3;
  align-items: flex-start;
  padding-top: var(--dl-layout-space-halfunit);
  padding-left: var(--dl-layout-space-unit);
  border-radius: var(--dl-layout-radius-radius8);
  padding-right: var(--dl-layout-space-unit);
  flex-direction: row-reverse;
  padding-bottom: var(--dl-layout-space-halfunit);
  justify-content: flex-start;
}
 
.app-ductwork-button {
  width: var(--dl-layout-size-medium);
}
 
.app-ductwork-add-duct {
  fill: white;
  color: white;
  display: block;
  font-style: normal;
  font-weight: 600;
}
 
.app-ductwork-counter {
  gap: var(--dl-layout-space-halfunit);
  display: flex;
  align-self: center;
  align-items: center;
  flex-direction: row-reverse;
  justify-content: flex-start;
}
 
.app-ductwork-plus {
  width: var(--dl-layout-size-xsmall);
  height: var(--dl-layout-size-xsmall);
}
 
.app-ductwork-count {
  font-size: 14px;
  font-family: "Bricolage Grotesque";
  font-weight: 400;
  line-height: 1.15;
  text-transform: none;
  text-decoration: none;
}
 
.app-ductwork-minus {
  width: var(--dl-layout-size-xsmall);
  height: var(--dl-layout-size-xsmall);
}
 
.app-ductwork-text2 {
  display: inline-block;
}
 
.app-ductwork-text3 {
  display: inline-block;
}
 
.app-ductworkroot-class-name {
  top: var(--dl-layout-space-sixunits);
  left: var(--dl-layout-space-sixunits);
  position: fixed;
}
